export default {
	props: ['disabled', 'dataList', 'dataListselectList', 'marginBottomShow', 'type', 'dataType', 'isTypeCust'],
	data() {
		return {
			dataLists: [],
			current: -1, // 用户选择的项
			checkedItems:  this.Static+'publish/createBusiness/checkedItems.png', // 选择后的图片对勾标注
			selectEd:  this.Static+'publish/createBusiness/selectEd.png', // 勾选圆框 黑色
			select:  this.Static+'publish/createBusiness/select.png', // 未勾选圆框 白色
			rhx:  this.Static+'publish/createBusiness/rhx.png', // 测试用途
			selectStyle: {
				// 选择之后样式边框变化
				background: '#FFFFFF',
				border: '1px solid #02C2A2',
				boxShadow: '0 1px 3px 0 rgba(2,194,162,0.30)'
			},
			noSelectStyle: {
				// 选择之前样式边框变化
				background: '#FFFFFF',
				border: '1px solid #E2E2E2'
			},
			selectStyle2: {
				// 选择之后样式边框变化
				background: '#02C2A2',
				color: '#FFFF'
			},
			noSelectStyle2: {
				// 选择之前样式边框变化
				color: '#2E2E30'
			}
		};
	},
	components: {},
	computed: {},
	watch: {
		dataList: {
			handler(a, b) {
				// console.log(a, b, '--------------------------------------dataList---------------------------------------');
				this.dataLists = a;
				this.dataLists.map((items, index) => {
					if (!items.valued) {
						items.valued = String(index);
					}
				});
			},
			deep: true
		}
	},
	created() {
		this.dataLists = this.dataList;
		console.log(this.dataLists, '----------------this.dataLists----------------');
		this.dataLists.map((items, index) => {
			items.valued = String(index);
			// 实例保障和支付方式的双向保定
			if (this.dataListselectList) {
				this.dataListselectList.map((item, inde) => {
					if (item.key === items.key && this.dataType === items.type) {
						this.current = index;
						this.$emit('tap-Radio', items); // 将用户选择下来的子项返给前台
					}
				});
			}
			// 图片选择和样式选择双向绑定
			if (this.isTypeCust) {
				this.dataLists.map((items, index) => {
					if (items.select === true) {
						this.current = index;
					}
				});
			}
		});

		console.log(this.dataLists, '--------------------marginBottomShow---------------------');
	},
	mounted() {},
	methods: {
		radioChange: function(evt) {
			console.log(evt, '---------------------evt-----------------------');
			this.dataLists.map((items, index) => {
				if (String(items.valued) === String(evt.target.value)) {
					this.current = index;
					items.select = true;
					// 图片选择和样式选择双向绑定
					if (this.isTypeCust) {
						this.$emit('tap-Radio', [items, this.current]); // 将用户选择下来的子项返给前台
					} else {
						this.$emit('tap-Radio', items); // 将用户选择下来的子项返给前台
					}
				}
			});
		}
	}
};
